import $ from 'jquery';

const valueList = $('.currency-selector__value');
const valueSelected = $('.currency-selector__value_selected');

console.log(valueList);

$('.currency-selector').click(function () {
	Array.prototype.forEach.call(valueList, function (el) {
		el.style.display = 'block';
	});
	for (let i = 0; i < valueList.length; i++) {
		$(valueList[i]).click(function () {
			console.log('value');
			Array.prototype.forEach.call(valueList, function (el) {
				console.log('none');
				el.style.display = 'none';
			});
			valueSelected.removeClass('currency-selector__value_selected');
			$(this).addClass('currency-selector__value_selected');
		});
	}
});




